const pageTitles = {
  home: '',
  about: '',
  logview: '',
  visits: '',
  apilist: '',
  organization: ''
}
const topMenus = [{
  name: 'home',
  text: pageTitles.home,
  icon: 'ios-home'
}, {
  name: 'about',
  text: '',
  icon: 'ios-help-circle'
}]
const leftMenus = {
  home: [{
    text: '',
    children: [{
      name: 'organization',
      text: pageTitles.organization,
      icon: 'ios-people-outline',
      color: '#6e92ce'
    }, {
      name: 'logview',
      text: pageTitles.logview,
      icon: 'ios-paper-outline',
      color: '#0c9'
    }, {
      name: 'visits',
      text: pageTitles.visits,
      icon: 'ios-pulse-outline',
      color: '#39f'
    }, {
      name: 'apilist',
      text: pageTitles.apilist,
      icon: 'ios-barcode-outline',
      color: '#57C534'
    }]
  }],
  about: [{
    text: '',
    children: [{
      name: 'about',
      text: pageTitles.about,
      icon: 'ios-information-circle'
    }]
  }]
}

export default {
  activeTopMenu: '',
  activeLeftMenu: '',
  topMenus,
  leftMenus,
  pageTitles,
  curPageName: ''
}



